<template>
  <div>
    <h4>
      <i class="el-icon-link"></i> <span v-if="!readonly"></span><span v-else></span>
      <el-button size="mini" v-if="addBtn && !readonly" @click="listRowAdd"></el-button>
    </h4>
    <fast2-theme-provider :option="readonly ? readOption : option" theme="block" :version="version">
      <template v-slot="component">
        <div v-if="component.option && component.option.column && component.option.column.length">
          <avue-crud :data="fileList" :option="component.option" @row-del="handleDelete">
            <template v-slot:fileAction="scope">
           
              <div v-if="!readonly && downloadClient">
	         <fast2-upload
                @file-success="fileSuccess"
                @file-progress="onFileProgress"
                @file-error="onFileError"
              ></fast2-upload>
                <!-- <el-upload
                  id="webpicker"
                  :show-file-list="false"
                  class="upload-box"
                  ref="uploadBox"
                  :http-request="uploadFile"
                  action="/"
                >
                  <!-- :before-upload="uploadFile" -->
                <!-- action="http://cs.51qqt.com/ELSServer_SRM/rest/servlet/UploadServlet"
                  @on-success="handleSuccess" -->
                <!-- <el-link
                    size="small"
                    slot="trigger"
                    type="primary"
                    @click="setUploadRow(scope.row)"
                  >
                    
                  </el-link> -->

                <!--item-->
                <!-- <div id="uploader">
                    <div id="picker"></div>
                    <button id="ctlBtn" @click="uploadFile" class="btn btn-default">
                      
                    </button>
                  </div> -->
                <!-- <template v-if="scope.row.attachmentUrl">
                    /
                    <el-link
                      v-if="scope.row.attachmentUrl"
                      download
                      target="_blank"
                      :href="`${dome}opt/nfsshare/${scope.row.attachmentUrl}`"
                    >
                      <!-- @click.stop="handleDownload(scope.row)"
                      
                    </el-link>
                  </template>
                </el-upload> -->
              </div>
              <div v-else-if="downloadClient">
                <el-link
                  v-if="scope.row.attachmentUrl"
                  download
                  target="_blank"
                  :href="`${dome}opt/nfsshare/${scope.row.attachmentUrl}`"
                >
                  
                </el-link>
              </div>
            </template>
            <template slot="annexType" slot-scope="{ row }">
              <span class="annexType" v-if="row.isRequired === 'Y'"></span>{{ row.annexType }}
            </template>
          </avue-crud>
        </div>
      </template>
    </fast2-theme-provider>
  </div>
</template>

<script>
import uploadApi from '../../lib/api/upload';
import { formatDate } from '../../lib/utils'; // , renderSize
import { validateNull } from '../../lib/validate';
import { getUserInfo } from '../../lib/auth';
import Uploader from './uploader';

export default {
  name: 'AttachmentList',
  props: {
    // 
    downloadClient: {
      type: Boolean,
      default: true
    },
    //  tab
    passClient: {
      type: Boolean,
      default: true
    },
    // 
    client: {
      type: Boolean,
      default: true
    },
    // tab
    clientTab: {
      type: Boolean,
      default: true
    },
    version: {
      type: String,
      default: () => {
        return 'attahcment-fiels_4_3';
      }
    },
    domain: {
      type: String,
      default: () => {
        return null;
      }
    },
    readonly: {
      type: Boolean,
      default: false
    },
    menu: {
      type: Boolean,
      default: true
    },
    id: {
      type: String,
      default: null
    },
    elsAccount: {
      type: String,
      default: null
    },
    attachmentTemplate: {
      type: Array,
      default: function () {
        return [];
      }
    },
    businessElsAccount: {
      type: String,
      default: null
    },
    businessModule: {
      type: String,
      default: null
    },
    addBtn: {
      type: Boolean,
      default: true
    },
    delBtn: {
      type: Boolean,
      default: true
    },
    option: {
      type: Object,
      default: () => {
        return {
          editBtn: false,
          addBtn: false,
          delBtn: true,
          cancelBtn: false,
          showSummary: false,
          rowKey: 'businessItemId',
          column: [
            {
              label: '',
              prop: 'fileAction',
              slot: true
            },
            {
              label: '',
              prop: 'isValidity',
              hide: true
            },
            {
              label: '',
              prop: 'annexType',
              type: 'select',
              span: 8,
              dicName: 'annex-type',
              datatype: 'remoteDic',
              orderIndex: 0,
              order: 1,
              slot: true
            }
          ]
        };
      }
    },
    readOption: {
      default: () => {
        return {
          menu: false,
          editBtn: false,
          addBtn: false,
          delBtn: false,
          cancelBtn: false,
          showSummary: false,
          column: [
            {
              label: '',
              prop: 'fileAction',
              slot: true
            },
            {
              label: '',
              prop: 'isValidity',
              hide: true
            },
            {
              label: '',
              prop: 'annexType',
              type: 'select',
              span: 8,
              dicName: 'annex-type',
              datatype: 'remoteDic',
              orderIndex: 0,
              order: 1,
              slot: true
            }
          ]
        };
      }
    }
  },
  data() {
    return {
      currentElsAccount: '',
      dome: '',
      showUpdate: true,
      uploadRow: null,
      fileList: [],
      businessId: '',
      uploader: null
    };
  },
  created() {},
  mounted() {
    const currentUserInfo = getUserInfo();
    this.currentElsAccount = currentUserInfo.elsAccount;
    // this.initWebUpload();
    if (!validateNull(this.id)) {
      this.businessId = this.id;
      this.initData();
    }
    if (window.location.host.includes('uat')) {
      this.dome = 'https://uat.51qqt.com/';
    } else {
      // uat  v4 
      this.dome = 'http://cs.51qqt.com/';
    }
    if (this.domain) {
      this.dome = this.domain;
    }
  },
  watch: {
    id(newValue) {
      this.businessId = newValue;
      this.initData();
    },
    attachmentTemplate() {
      this.initData();
    },
    fileList(newVal) {
      console.log(newVal);
    }
  },
  methods: {
    doAction(action, data) {
      this.$emit(action, data);
      // this.$message.success(action)
    },

    handleDelete(row, index) {
      if (!row.uuid) {
        this.fileList.splice(index, 1);
        return;
      }
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        const action = 'delete';
        if (row.uuid) {
          uploadApi.attachmentServer(action, { uuid: row.uuid }).then((res) => {
            if (res.data.statusCode !== '200') {
              this.$message.error(res.data.message);
              return;
            }
            this.fileList.splice(index, 1);
          });
        }
      });
    },

    handleDownload(row) {
      console.log(row);
    },

    handleSuccess(response, file, fileList) {
      console.log(response, file, fileList);
    },

    download(blobUrl, fileName) {
      const a = document.createElement('a');
      a.download = fileName;
      a.href = blobUrl;
      a.click();
      document.body.removeChild(a);
    },

    // 
    downloadFile(row) {
      if (row.attachmentUrl) {
        const params = {
          elsAccount: row.elsAccount,
          elsSubAccount: row.elsSubAccount,
          filePath: `${this.dome}${row.attachmentUrl}`
        };
        uploadApi.download(params).then((res) => {
          if (res.data.statusCode !== '200') {
            this.$message.error(res.data.message);
          }
        });
      }
    },

    async getBase64(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        let fileResult = '';
        reader.readAsDataURL(file); // 
        reader.onload = function () {
          fileResult = reader.result;
        }; //  
        reader.onerror = function (error) {
          reject(error);
        }; //     resolve 
        reader.onloadend = function () {
          resolve(fileResult);
        };
      });
    },

    getList() {
      const action = 'findList';
      const params = {
        businessElsAccount: this.businessElsAccount,
        businessModule: this.businessModule,
        businessId: this.businessId
      };
      uploadApi.attachmentServer(action, params).then((res) => {
        if (res.data.pageData && res.data.pageData.rows) {
          if (this.passClient) {
            this.fileList = res.data.pageData.rows;
          } else {
            if (this.client && this.clientTab) {
              this.fileList = res.data.pageData.rows.filter(
                (item) => item.elsAccount === this.elsAccount
              );
            } else if (this.client && !this.clientTab) {
              this.fileList = res.data.pageData.rows.filter(
                (item) => item.elsAccount !== this.elsAccount
              );
            } else if (!this.client && !this.clientTab) {
              this.fileList = res.data.pageData.rows.filter(
                (item) => item.elsAccount === this.businessElsAccount
              );
            } else if (!this.client && this.clientTab) {
              this.fileList = res.data.pageData.rows.filter(
                (item) => item.elsAccount !== this.businessElsAccount
              );
            }
          }
        }
        // businessItemId  push
        const itemIds = this.fileList.map((item) => item.businessItemId);
        this.attachmentTemplate.forEach((item) => {
          // item.isValidity = true; // TODO:
          if (itemIds.includes(item.businessItemId)) {
            const index = this.fileList.findIndex(
              (file) => file.businessItemId === item.businessItemId
            );
            const i = this.fileList[index];
            item.isValidity === 'Y' && this.readonly === false
              ? (i.$cellEdit = true)
              : (i.$cellEdit = false);
            i.isValidity = validateNull(item.isValidity) ? false : item.isValidity;
            i.startValidity = validateNull(item.startValidity) ? '' : item.startValidity;
            i.endValidity = validateNull(item.endValidity) ? '' : item.endValidity;
            this.fileList[index] = {
              ...item,
              ...i
            };
          } else {
            item.isValidity === 'Y' && this.readonly === false
              ? (item.$cellEdit = true)
              : (item.$cellEdit = false);
            // item.isValidity = validateNull(item.isValidity) ? false : item.isValidity;
            item.startValidity = validateNull(item.startValidity) ? '' : item.startValidity;
            item.endValidity = validateNull(item.endValidity) ? '' : item.endValidity;
            this.fileList.push(item);
          }
        });
        console.log('this.fileList', this.fileList);
      });
    },

    initData() {
      this.showUpdate = !validateNull(this.id);
      if (!validateNull(this.businessId)) {
        this.option.delBtn = this.delBtn;
        this.getList();

        /*
        const dic = this.$getDicItem('attachmentType');
        this.readOption.column.unshift({
          label: '',
          prop: 'attachmentType',
          type: 'select',
          order: 0,
          dicData: dic
        });
        */
      }
    },

    // 
    initList() {
      // 
      this.attachmentTemplate.map((item) => {
        this.listTempRowAdd(item);
      });
      //  id  id 
      if (this.id) {
        this.getList();
        this.$forceUpdate();
      }
    },

    initWebUpload() {
      /*
      setTimeout(() => {
        // 3
        // var state = 'pending'; // 
        this.uploader = WebUploader.create({
          swf: './webuploader-0.1.5/Uploader.swf',
          server: 'http://cs.51qqt.com/ELSServer_SRM/rest/servlet/UploadServlet',
          pick: '#picker',
          resize: false
        });
        this.uploader.on('fileQueued', function (file) {
          console.log('fileQueued');
          console.log(file);
        });
        this.uploader.on('uploadProgress', function (file, percentage) {
          console.log('progress ==', percentage * 100 + '%');
        });
        this.uploader.on('uploadSuccess', function (file) {
          console.log('uploadSuccess');
        });
        this.uploader.on('uploadError', function (file) {
          console.log('uploadError');
        });
        this.uploader.on('uploadComplete', function (file) {
          console.log('uploadComplete');
        });
      });
      */
    },

    listRowAdd() {
      const fileItem = {
        elsAccount: this.elsAccount,
        businessItemId: this.id + '_00' + (this.fileList.length + 1)
      };
      this.fileList.push(fileItem);
    },

    listTempRowAdd(item) {
      if (!item) {
        return;
      }
      const type = item.attachmentType || item.fileType;
      if (!type) {
        return;
      }
      const fileItem = {
        businessItemId: this.id + type + (this.fileList.length + 1),
        attachmentType: type
      };
      this.fileList.push(fileItem);
    },

    /*
    removeFile() {
      const action = 'delete';
      if (this.uploadRow.uuid) {
        uploadApi.attachmentServer(action, { uuid: this.uploadRow.uuid });
      }
    },
    */

    sendFiles() {
      const list = this.fileList.filter((item) => item.uuid); // 
      return new Promise((resolve, reject) => {
        if (list.length > 0) {
          const params = {
            businessItemIds: list.map((item) => item.uuid).toString()
          };
          uploadApi
            .sendFiles(params)
            .then((res) => {
              if (res.data.statusCode === '200') {
                resolve({ result: true, statusCode: res.data.statusCode });
              } else {
                resolve({
                  result: false,
                  message: res.data.message,
                  statusCode: res.data.statusCode
                });
              }
            })
            .catch((res) => {
              reject(res);
            });
        } else {
          // eslint-disable-next-line prefer-promise-reject-errors
          resolve({ result: true, statusCode: '200' });
          // reject({
          //   result: false,
          //   message: '',
          //   statusCode: '-100'
          // });
        }
      });
    },

    setUploadRow(row) {
      this.uploadRow = row;
    },
    fileSuccess(rootFile, file, message, chunk) {
      console.log(message);
      const data = JSON.parse(message)?.data;
      //code
      const fileData = {
        fileSize: data.size,
        fileName: data.name,
        fileType: data.type,
        filePath: data.url
      };
      this.$emit('upload-file', fileData);
      this.updateFileList(fileData);
    },
    onFileProgress() {
      console.log('');
    },
    onFileError() {
      this.$message.error('');
    },
    updateFileList(file) {
      const _this = this;
      const action = _this.uploadRow.attachmentName
        ? 'update'
        : _this.uploadRow.uuid
        ? 'update'
        : 'create';
      const checkProp = 'businessItemId';
      console.log('this.elsAccount', this.elsAccount);
      let itemRow = {
        ...this.uploadRow,
        elsAccount: this.elsAccount,
        attachmentType: file.fileType,
        attachmentSize: file.fileSize,
        attachmentName: file.fileName,
        attachmentDesc: file.fileName,
        attachmentUrl: file.filePath,
        businessModule: this.businessModule,
        businessElsAccount: this.businessElsAccount,
        businessId: this.businessId
      };
      const uploadRow = {
        ...this.uploadRow,
        elsAttachmentVOList: [itemRow]
      };
      delete uploadRow.createDate;
      uploadApi.attachmentServer(action, uploadRow).then((res) => {
        if (res.data.statusCode === '200') {
          const elsAttachment = res.data.data.elsAttachmentVOList[0];
          console.log('elsAttachment', uploadRow, this.fileList, elsAttachment);
          itemRow = {
            ...itemRow,
            ...elsAttachment,
            attachmentType: file.fileType,
            elsAccount: res.data.data.elsAccount,
            elsSubAccount: res.data.data.elsSubAccount,
            createUser: elsAttachment.createUser,
            createDate: formatDate(new Date(elsAttachment.createDate), 'yyyy-MM-dd hh:mm:ss')
          };
          this.fileList = this.fileList.map((item) => {
            return item[checkProp] === uploadRow[checkProp] ? itemRow : item;
          });
          this.$message.success('');
        } else {
          this.$message.error(res.data.message);
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.annexType::after {
  content: '*';
  color: #f56c6c;
  margin-right: 4px;
}
</style>
