/**
 *
 * @param {*} remoteColumn column 
 * @param {*} localColum  column 
 */
export const mergeColumn = (remoteColumn = [], localColum = []) => {
  // 
  let newColumn = remoteColumn;
  // 
  localColum.map(item => {
    newColumn = newColumn.map(subitem => {
      let newSubItem = subitem;
      if (subitem.prop === item.prop) {
        newSubItem = _.merge(subitem, item);
      }
      return newSubItem;
    });
    const isExist = newColumn.find(subitem => {
      return subitem.prop === item.prop;
    });
    if (!isExist) {
      newColumn.push(item);
    }
  });
  return newColumn;
};

/**
 *
 * @param {} data
 * @param {[1, 2]} arr
 * @param {} filed
 * @param {id} id
 * @param {} columnIndex
 * @param {} row
 */
export function mySpanMethod(data, arr, filed, id, columnIndex, row) {
  // lodashgroupBy

  const itemArr = []; // 
  data.forEach(item => {
    if (item[filed] === row[filed]) {
      itemArr.push(item);
    }
  });
  for (let i = 0; i < arr.length; i++) {
    if (columnIndex === arr[i]) {
      // itemArr
      const rowFiled = row[filed];
      const itemFiled = itemArr[0][filed];
      const rid = row[id];
      const itemId = itemArr[0][id];
      if (rowFiled === itemFiled && rid === itemId) {
        return {
          rowspan: itemArr.length,
          colspan: 1
        };
      } else {
        return {
          rowspan: 0,
          colspan: 0
        };
      }
    }
  }
}

export const formatDate = (date, fmt) => {
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(
      RegExp.$1,
      (date.getFullYear() + "").substr(4 - RegExp.$1.length)
    );
  }
  const o = {
    "M+": date.getMonth() + 1,
    "d+": date.getDate(),
    "h+": date.getHours(),
    "m+": date.getMinutes(),
    "s+": date.getSeconds()
  };
  for (const k in o) {
    if (new RegExp(`(${k})`).test(fmt)) {
      const str = o[k] + "";
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1.length === 1 ? str : padLeftZero(str)
      );
    }
  }
  return fmt;
};
function padLeftZero(str) {
  return ("00" + str).substr(str.length);
}

/// JS
/// <param name="filesize">,bytes</param>
/// <returns></returns>
export const renderSize = filesize => {
  if (filesize === null || filesize === "") {
    return "0 Bytes";
  }
  const unitArr = ["Bytes", "KB", "MB", "GB", "TB", "PB", "EB", "ZB", "YB"];
  let index = 0;
  const srcsize = parseFloat(filesize);
  index = Math.floor(Math.log(srcsize) / Math.log(1024));
  let size = srcsize / Math.pow(1024, index);
  size = size.toFixed(2); // 
  return size + unitArr[index];
};

export const getAccount = () => {
  const userInfo = getStore({ name: "userInfo", timer: 1200 }); // getStore 
  if (userInfo && userInfo.elsAccount) {
    return userInfo;
  }
  return {
    elsAccount: "",
    elsSubAccount: "",
    token: ""
  };
};

